import React from 'react'
import {Row,Container,Col} from 'react-bootstrap';
import {Link} from 'react-router-dom'
import './menu.css'

function Menu() {
         return (
                  <Container>
                  <Row className="justify-content-center" style={{height:"100vh"}}>
                       <Col className="menu text-center" xl={5}>
                       <h1 className='mb-5'>Sudako</h1>
          <button className="b mt-4 mb-5"><Link className="m" to="/easy">Easy</Link></button>
          <button className="b mb-5"><Link className="m" to="/medium">medium</Link></button>
          <button className="b"><Link className="m" to="/hard">Hard</Link></button>                      
                       </Col>    
                  </Row>
                  </Container>
         )
}

export default Menu